.register-verify-container {
    max-width: 500px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
}

h1 {
    text-align: center;
}

.input-group {
    margin-bottom: 5px;
}

.input-group-info {
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    gap: 10px;
}

