    padding: 10px;
    margin: 5px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;
}

button {
    width: 100%;
    padding: 10px;
    background-color: #0068ff;
    color: white;
    border: none;
    border-radius: 4px;
    margin-top: 5px;
    font-size: 20px;
    cursor: pointer;
}

button:hover {
    background-color: #45a049;
}

.error-message, .success-message {
    margin-bottom: 15px;
    padding: 10px;
    border-radius: 4px;
    text-align: center;
    font-size: 14px;
}

.error-message {
    background-color: #f8d7da;
