body {
    background-image: url('../image/backgorund2.webp'); /* ƒê∆∞·ªùng d·∫´n ƒë·∫øn ·∫£nh n·ªÅn */
    background-size: cover; /* T·ª± ƒë·ªông ƒëi·ªÅu ch·ªânh k√≠ch th∆∞·ªõc ·∫£nh ƒë·ªÉ ph·ªß to√†n b·ªô trang */
    background-position: center; /* ƒê·∫∑t ·∫£nh n·ªÅn ·ªü gi·ªØa */
    background-repeat: no-repeat; /* Kh√¥ng l·∫∑p l·∫°i ·∫£nh n·ªÅn */
}

.d-flex {
    background-color: rgba(240, 248, 255, 0.3); 
}


/* Container for the login page */
.login-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #8fc5f0;
    font-family: Arial, sans-serif;
}

/* Box for the login form */
.login-box {
    background: white;
    padding: 30px 40px;
    border-radius: 50px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 350px;
}

/* Title and subtitle */
.login-box h2 {
    font-size: 24px;
    color: #0068ff;
    margin-bottom: 10px;
}

.login-box p {
    font-size: 14px;
    color: #4a4a4a;
    margin-bottom: 20px;
}

/* ƒê·∫£m b·∫£o n·ªÅn tab active v√† hover ƒë√∫ng */
.nav-pills .nav-item .nav-link {
    position: relative;
    z-index: 1; /* ƒê·∫£m b·∫£o r·∫±ng n·ªÅn kh√¥ng b·ªã che m·∫•t */
    transition: background-color 0.3s, color 0.3s;
}

.nav-pills .nav-item .nav-link:hover {
    background-color: #0051cc; /* ƒê·ªïi m√†u khi hover */
    color: #ffffff; /* ƒê·ªïi m√†u ch·ªØ khi hover */
}

.nav-pills .nav-item .nav-link.active {
    background-color: #0051cc; /* M√†u n·ªÅn khi active */
    color: white; /* M√†u ch·ªØ khi active */
    z-index: 2; /* ƒê·∫£m b·∫£o r·∫±ng tab active c√≥ ƒë·ªô ∆∞u ti√™n hi·ªÉn th·ªã */
}

/* Input group styling */
.input-group {
    margin-bottom: 15px;
    text-align: left;
}

.input-group label {
    display: block;
    font-size: 14px;
    margin-bottom: 5px;
    color: #333;
}

.input-group input {
    width: 100%;
    padding: 10px;
    font-size: 14px;
    border: 1px solid #dcdcdc;
    border-radius: 5px;
    box-sizing: border-box;
}

/* Button styling */
.btn-login {
    width: 100%;
    background-color: #0068ff;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    margin-top: 10px;
}

.btn-login:hover {
    background-color: #0051cc;
}

/* Error message styling */
.error-message {
    color: red;
    font-size: 12px;
    margin-bottom: 10px;
}

/* Extra options below login */
.extra-options {
    margin-top: 15px;
}

.extra-options a {
    font-size: 12px;
    color: #0068ff;
    text-decoration: none;
    margin: 0 5px;
}

.extra-options a:hover {
    text-decoration: underline;
}

/* Hi·ªáu ·ª©ng loading khi h√†nh ƒë·ªông */
.loading-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.error-message {
    margin-bottom: 15px;
    padding: 10px;
    border-radius: 4px;
    text-align: center;
    font-size: 14px;
}

.error-message {
    background-color: #f8d7da;
    color: #721c24;
}

/* Bi·ªÉu t∆∞·ª£ng b√°nh rƒÉng xoay */
.spinner {
    width: 50px;
    height: 50px;
    border: 6px solid rgba(255, 255, 255, 0.3);
    border-top-color: white;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

/* VƒÉn b·∫£n "ƒêang ƒëƒÉng nh·∫≠p..." v√† "ƒêang ƒëƒÉng xu·∫•t..." */
.loading-text {
    color: white;
    font-size: 20px;
    margin-top: 10px;
}

/* Keyframes cho hi·ªáu ·ª©ng xoay */
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}


