.reactions {
  display: flex;
  justify-content: flex-start;
  /* CƒÉn tr√°i */
  gap: 5px;

  position: relative;
}

.emoji {

  font-size: 15px;
  cursor: pointer;
  transition: transform 0.2s ease, opacity 0.3s ease;
  opacity: 0;
  /* ·∫®n c√°c emoji m·∫∑c ƒë·ªãnh */
}

.emoji:hover {
  transform: scale(1.2);
  /* Ph√≥ng to khi di chu·ªôt */
}

.reactions .like {
  position: absolute;
  right: -10px;
  top: 20px;
  opacity: 0.5;
  /* Ch·ªâ hi·ªÉn th·ªã n√∫t like */
}

.reactions:hover .emoji {
  opacity: 1;
  /* Hi·ªÉn th·ªã t·∫•t c·∫£ emoji khi hover v√†o container reactions */
}

.emoji.active {
  color: #007bff;
  /* Th√™m m√†u s·∫Øc cho reactions ƒë√£ ch·ªçn */
}



.close-reactions {
  position: relative;
  bottom: 5px;
  font-size: 20px;
  cursor: pointer;
  color: red;
  opacity: 0;
  transition: opacity 0.3s ease, transform 0.2s ease;
  margin-left: 1px;
  /* C√°ch bi·ªát n√∫t x v·ªõi reaction cu·ªëi */
}

.reactions:hover .emoji,
.reactions:hover .close-reactions {
  opacity: 1;
}

.close-reactions:hover {
  transform: scale(1.2);
}


.reaction-group {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  position: relative;
}


.reaction-icons-wrapper {
  position: relative;
  top: 20px;
  gap: 6px;
  padding: 4px 10px;
  border: 1.5px solid #d1d1d1;
  border-radius: 20px;
  background-color: #f5f5f5;
  display: inline-flex;
  align-items: center;
}

.reaction-icon {
  font-size: 1rem;
  position: relative;
  display: flex;
  align-items: center;
  cursor: default;
  user-select: none;
}

.reaction-count-small {
  font-size: 0.65rem;
  margin-left: 3px;
  color: #444;
  font-weight: normal;
  vertical-align: top;
}

.total-reactions-badge {
  font-size: 0.75rem;
  font-weight: bold;
  color: #555;
  background: #ddd;
  border-radius: 10px;
  padding: 0 6px;
  margin-left: 8px;
  user-select: none;
  align-self: center;
}