/* Style cho modal t√¨m ki·∫øm */
.search-modal {
  position: fixed;
  top: 100px; /* Adjust this so it‚Äôs not at the top of the screen */
  right: 10px; /* Center the modal horizontally */
  transform: translateX(-50%); /* Center horizontally */
  background: rgba(255, 255, 255, 0.9); /* Semi-transparent background */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 9999; /* Ensure modal is always in front */
  border-radius: 10px;
  max-width: 250px;
  width: 100%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.search-modal .modal-content {
  background: white;
  padding: 10px;
  border-radius: 10px;
  max-width: 250px;
  width: 100%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.search-modal input {
  width: 100%;
  padding: 10px;
  margin-bottom: 5px;
  border: 1px solid #e4e3e3;
  border-radius: 5px;
  font-size: 15px;
}

.search-modal button {
  padding: 10px 20px;
  background-color: #68affa;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
}

.search-modal button:hover {
  background-color: #4991dd;
}

